.worksh2,
.modalwindow,
* {
  animation: fadeIn 0.3s;
}

.navv {
  animation: fadeIn 0.8s;
}

.fuwafuwa {
  -webkit-animation: fuwafuwa 3s infinite linear alternate;
  animation: fuwafuwa 3s infinite linear alternate;
}

.buruburu {
  -webkit-animation: buruburu 0.1s infinite linear alternate;
  animation: buruburu 0.1s infinite linear alternate;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fuwafuwa {
  0% {
    transform: translate(0, 0) rotate(-5deg);
  }
  50% {
    transform: translate(0, -5px) rotate(0deg);
  }
  100% {
    transform: translate(0, 0) rotate(5deg);
  }
}

@keyframes buruburu {
  0% {
    transform: translate(0, 0) rotate(-3deg);
  }
  50% {
    transform: translate(0, -1px) rotate(0deg);
  }
  100% {
    transform: translate(0, 0) rotate(3deg);
  }
}

@keyframes bg-color {
  0% {
    background-color: #e15b5b;
  }
  20% {
    background-color: #ab5be1;
  }
  40% {
    background-color: #5b6de1;
  }
  60% {
    background-color: #5bcfe1;
  }
  80% {
    background-color: #d8e15b;
  }
  100% {
    background-color: #5be16d;
  }
}
/*monster
--------------------------------------------------------------------------------------*/
.mon1 {
  width: 300px;
  position: absolute;
  top: 20%;
  left: 1%;
  z-index: 0;

  -webkit-animation: fuwafuwa 3s infinite linear alternate;
  animation: fuwafuwa 3s infinite linear alternate;
  display: none;
}

.mon2 {
  width: 300px;
  position: absolute;
  top: 20%;
  right: 1%;
  z-index: 0;
  display: none;
  -webkit-animation: fuwafuwa 3s infinite linear alternate;
  animation: fuwafuwa 3s infinite linear alternate;
}

.mon3 {
  width: 300px;
  position: absolute;
  bottom: 0%;
  right: 1%;
  z-index: 0;
  display: none;
  -webkit-animation: fuwafuwa 3s infinite linear alternate;
  animation: fuwafuwa 3s infinite linear alternate;
}

.mon4 {
  width: 300px;
  position: absolute;
  bottom: 0;
  z-index: 0;
  left: 1%;
  display: none;
  -webkit-animation: fuwafuwa 3s infinite linear alternate;
  animation: fuwafuwa 3s infinite linear alternate;
}
.mon5 {
  width: 300px;
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 0;

  display: none;
}
.mon6 {
  width: 300px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 0;
  display: none;
}

.monfix1 {
  width: 200px;
  position: fixed;
  top: 20%;
  left: 0;
  -webkit-animation: fuwafuwa 3s infinite linear alternate;
  animation: fuwafuwa 3s infinite linear alternate;
  display: none;
}

.monfix2 {
  width: 200px;
  position: fixed;
  bottom: 10%;
  right: 0;
  -webkit-animation: fuwafuwa 3s infinite linear alternate;
  animation: fuwafuwa 3s infinite linear alternate;
  display: none;
}
