/*WorksCard
----------------------------------------------*/

.MainBg {
  background-color: cornflowerblue;
  height: 100vh;
}
.workscard img {
  border-radius: 10px 10px 0 0;
}
.workscard {
  display: block;
  text-align: center;
  flex-basis: 30%;
  margin: 30px 40px;
  background-color: #fff;
  border-radius: 10px;
  transition: box-shadow 0.5s;
  transition: translate 0.5s;
}
.worksname {
  padding: 20px 0;
  color: #fff;
  font-weight: 600;
}
.WorksFlex {
  flex-wrap: wrap;
  justify-content: center;
}

.worksh2 {
  font-size: 2rem;
  color: #5d83ff;
  text-align: center;
  font-weight: 600;
  margin-top: 40px;
  margin-bottom: 40px;
}

.workscard:hover {
  box-shadow: 0 12px 12px gray;
  transform: translate(0, -5px);
}
/*Modal
------------------------------------------------------------------------------*/

.modalContent {
  position: fixed;
  z-index: 999;
  width: 70%;
  height: 70%;
  padding: 40px;
  background-color: white;
  left: 50%;

  transform: translateX(-50%);
  top: 10%;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
}

.back {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.5);
}

.modalContent img {
  display: block;

  object-fit: contain;
}

.Mtext {
  text-align: center;
  padding-top: 20px;
  overflow: wrap;
}
.Mtext p {
  margin-bottom: 40px;
}
.Mname {
  font-size: 2rem;
  font-weight: 600;
}

.Murl a {
  color: black;
}

