/*responsive
---------------------------------------------------------------------------------*/
.hamburger {
  margin-top: 30px;
  margin-right: 30px;
  width: 30px;
  height: 20px;
  cursor: pointer;
  display: none;
  transition: 1s;
  z-index: 999;
}
.inner_line {
  display: block;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
  transition: 1s;
  border-radius: 4px;
}
#line2 {
  margin-top: 4px;
}
#line3 {
  margin-top: 4px;
}
.navv {
  display: none;
}
.in {
  position: fixed;
  display: block;
}

#nav {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  z-index: 800;
}

#nav li {
  text-align: center;
  margin-top: 80px;
}

.batu1 {
  transform: rotate(45deg);
  margin-top: 3px;
  position: absolute;
}

.batu2 {
  transform: rotate(-45deg);
  position: absolute;
}

.shokyo {
  display: none;
}

.mae {
  z-index: 1000;
}

.resabout {
  text-align: center;
}
.resabout .shunD {
  font-weight: 600;
  font-size: 1.5rem;
  color: rgb(53, 53, 53);
}

.resabout p {
  line-height: 2;
}

.resabout button {
  display: block;
  padding: 10px 20px;
  background-color: rgb(53, 53, 53);
  color: white;
  border-radius: 5px;
  margin: 0 auto;
}

.resabout .form {
  margin-top: 40px;
  background-color: #b7d6ff;
  border-radius: 5px;
  padding-top: 20px;
  padding-bottom: 20px;
  margin-bottom: 40px;
}

.resabout .SNS {
  display: block;
  margin: 0 auto;
  margin-bottom: 40px;
}

@media screen and (max-width: 959px) {
  /* 959px以下に適用されるCSS（タブレット用） */
  .wrapper {
    width: 600px;
  }

  .hamburger {
    display: block;
  }

  .Headernav {
    display: none;
  }

  .mon5,
  .mon6,
  .monfix1,
  .monfix2 {
    display: none;
  }

  h1 {
    font-size: 1rem;
    font-weight: bold;
  }

  .Headernav li {
    margin-left: 30px;
  }

  .slick-arrow::before {
    display: none;
  }

  .workscard {
    flex-basis: 50%;
  }

  /*--------------------------------------------*/

  .modalContent {
    position: fixed;
    z-index: 999;
    width: 70%;
    height: 70%;
    padding: 40px;
    background-color: white;
    display: flex;
    flex-direction: column;
    overflow-wrap: anywhere;
    top: 10%;
    left: 50%;

    transform: translateX(-50%);
    overflow-y: scroll;
  }

  .back {
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5);
  }

  .modalContent img {
    display: block;
    object-fit: contain;
  }

  .Mname {
    font-size: 2rem;
    font-weight: 600;
  }

  .Murl a {
    color: black;
  }
}
@media screen and (max-width: 570px) {
  /* 480px以下に適用されるCSS（スマホ用） */
  .wrapper {
    width: 95%;
  }

  footer {
    text-align: center;
    height: 80px;
    background-color: #4988c8;
    color: white;
    position: inherit;
    width: 100%;
  }

  .mon4,
  .mon3,
  .mon2,
  .mon1 {
    display: none;
  }

  .port {
    font-size: 1.5rem;
  }

  .modalContent {
    position: fixed;
    z-index: 999;
    width: 70%;
    height: 50%;
    padding: 40px;
    background-color: white;
    display: flex;
    flex-direction: column;
    overflow-wrap: break-word;
    overflow-wrap: anywhere;
    overflow-y: scroll;
  }
}
